<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix header">
            <span>订单回收站</span>
            <el-button type="primary" @click="$router.push('/users/create')" round>导出数据</el-button>
        </div>
        <div class="filter">
            <el-input placeholder="下单人" class="input-with-select" v-model="searchVal">
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
            <el-input placeholder="收货人" class="input-with-select" v-model="searchVal" style="margin-left: 20px">
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>

            <el-date-picker
            type="daterange"
            v-model="dateVal"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
            </el-date-picker>
        </div>
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column label="订单号" prop="order_sn"></el-table-column>
            <el-table-column label="下单时间" prop="add_time"></el-table-column>
            <el-table-column label="下单人" prop="username"></el-table-column>
            <el-table-column label="收货人" prop="consignee"></el-table-column>
            <el-table-column label="总额" prop="order_amount"></el-table-column>
            <el-table-column label="订单状态" prop="order_status">
                <el-tag>{{ tableData.order_status ? '已确认': '未确认' }}</el-tag>
            </el-table-column>
            <el-table-column label="支付状态" prop="pay_status">
                <el-tag>{{ tableData.pay_status ? '已付款': '未付款' }}</el-tag>
            </el-table-column>
            <el-table-column label="配送情况" prop="reg_time">
                <el-tag>{{ tableData.shipping_status ? '配送中': '未发货' }}</el-tag>
            </el-table-column>
        </el-table>

        <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="pagination.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pagination.total">
        </el-pagination>
    </el-card>
</template>
<script>
import {getOrdersListApi} from '@/api/orders.js'
export default {
    data() {
        return {
            pickerOptions: {
              shortcuts: [{
                text: '最近一周',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  picker.$emit('pick', [start, end]);
                }
              }, {
                text: '最近一个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  picker.$emit('pick', [start, end]);
                }
              }, {
                text: '最近三个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  picker.$emit('pick', [start, end]);
                }
              }]
            },
            searchVal: '',
            dateVal: '',
            pagination: {
                total: 0,
                currentPage: 1,
                pageSize: 5,
            },
            tableData: []
        }
    },
    created() {
        getOrdersListApi({
            is_delete: 1
        }).then(res => {
            console.log(res);
            this.tableData = res.data.list
            this.pagination.total = parseInt(res.data.total)
        })
    },
    methods: {
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pagination.pageSize = val
            getOrdersListApi({
                pagenum: this.pagination.currentPage,
                pagesize: val,
                is_delete: 1
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
            })
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
            this.pagination.currentPage = val
            getOrdersListApi({
                pagenum: val,
                pagesize: this.pagination.pageSize,
                is_delete: 1
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
            })
        }
    }
}
</script>
<style scoped>
    .el-card .filter {
        margin-bottom: 20px;
        overflow: hidden
    }
    .el-card .filter .el-input {
        width: 300px;
        float: left
    } 
    .el-card .filter .el-date-editor {
        float: right;
    }
    .el-pagination {
        width: 100%;
        padding: 10px 0;
        background: #ebeef5;
        text-align: right;
        margin-top: 10px;
        padding-right: 10px;
        box-sizing: border-box;
    }
    .el-card .header .el-button {
        float: right;
        margin-top: -10px
    }
</style>